ect,\n\t\t\trequired: true,\n\t\t},\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tloading: false,\n\t\t\tbus: new Vue(),\n\t\t}\n\t},\n\tcreated() {\n\t\tthis.signature = this.account.signature ? toHtml(detect(this.account.signature)).value : ''\n\t},\n\tmethods: {\n\t\tdeleteSignature() {\n\t\t\tthis.loading = true\n\n\t\t\tthis.$store\n\t\t\t\t.dispatch('updateAccountSignature', { account: this.account, signature: null })\n\t\t\t\t.then(() => {\n\t\t\t\t\tlogger.info('signature deleted')\n\t\t\t\t\tthis.signature = ''\n\t\t\t\t\tthis.loading = false\n\t\t\t\t})\n\t\t\t\t.catch((error) => {\n\t\t\t\t\tlogger.error('could not delete account signature', { error })\n\t\t\t\t\tthrow error\n\t\t\t\t})\n\t\t},\n\t\tsaveSignature() {\n\t\t\tthis.loading = true\n\n\t\t\tthis.$store\n\t\t\t\t.dispatch('updateAccountSignature', { account: this.account, signature: this.signature })\n\t\t\t\t.then(() => {\n\t\t\t\t\tlogger.info('signature updated')\n\t\t\t\t\tthis.loading = false\n\t\t\t\t})\n\t\t\t\t.catch((error) => {\n\t\t\t\t\tlogger.error('could not update account signature', { error })\n\t\t\t\t\tthrow error\n\t\t\t\t})\n\t\t},\n\t},\n}\n</script>\n\n<style lang=\"scss\" scoped>\n\n.ck.ck-editor__editable_inline {\n\twidth: 330px;\n\tmax-width: 78vw;\n\theight: 100px;\n\tborder-radius: var(--border-radius) !important;\n\tborder: 1px solid var(--color-border) !important;\n\tbox-shadow: none !important;\n}\n\n.primary {\n\tpadding-left: 26px;\n\tbackground-position: 6px;\n\tcolor: var(--color-main-background);\n\n\t&:after {\n\t\tleft: 14px;\n\t}\n}\n\n.button-text {\n\tbackground-color: transparent;\n\tborder: none;\n\tcolor: var(--color-text-maxcontrast);\n\tfont-weight: normal;\n\n\t&:hover,\n\t&:focus {\n\t\tcolor: var(--color-main-text);\n\t}\n}\n.section {\n\tdisplay: block;\n\tpadding: 0;\n\tmargin-bottom: 23px;\n}\n</style>\n<style>\n.ck-balloon-panel {\n\tz-index: 10000 !important;\n}\n</style>\n"],"sourceRoot":""}]);
